.message p
{
	display: block;
}

.message{
	padding: 5px;
	background-color: #aaa;
	border-radius: 5px;
	margin-left: 10px;
}

.user
{
	display:  flex;
	margin: 10px;
	padding: 5px;
	border-bottom: 1px gray solid;
	border-radius: 10px;
	align-items: center;
	justify-content: space-between;
	background-color: #fff9;
}

.user:hover
{
	background-color: #aaa;
}

.dialog
{
	display:  flex;
	flex-direction: column;
}

.dialog div
{
	display:  flex;

}

.dialog textarea
{
	margin: 10px;
	padding: 5px;
	box-sizing: border-box;
	border-bottom: 1px gray solid;
	border-radius: 10px;
	flex-grow: 1;
}