.friend img{
	width: 100px;
	height: 100px;
	border-radius: 100%;
}
.navbar .friend img{
	width: 50px;
	height: 50px;
	border-radius: 100%;
}

.friend {
	background-color: #423F78;
	display: flex;
	border-radius: 10px;
	padding: 3px;
	margin: 3px;
	justify-content: space-evenly;
	align-items: center;
}

.friend a{
	color: #ffffff;
	display: block;
	flex-grow: 1;
	text-align: center;
	padding: 3px;
}
.friend a:hover{
	background-color: #423F78;
}

.friend p{
	flex-grow: 1;
}

.friend button{
	flex-grow: 1;
}
