.me{

display: grid;

grid-template-columns: 3fr 9fr;

grid-gap: 10px;

}

.me img{

width: 100%;

border-radius: 10px;

}

.posts{

margin: 10px;

padding: 10px;

border-radius: 10px;

}

.posts img{

width: 40px;

margin: 5px;

border-radius: 5px;

}

.post{

margin: 20px 0;

}

.posts input{
	border-radius: 5px;
	padding:  5px;
	}

.posts button{
	border: 2px white solid;
	background-color: transparent;
	color:  white;
	border-radius: 5px;
	padding:  5px;
	}